.apis_counter {
  display: block;
  padding: 16px 56px 0px;
  flex-basis: 100%;
  font-family: 'Source Code Pro';
  font-size: 14px;
  color: #5f6368;
}
.apis_category {
  display: flex;
  flex-wrap: wrap;
  padding: 16px 28px 0px;
}
.apis_category__name {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  padding: 0px 28px 8px;
  flex-basis: 100%;
}

.message {
  text-align: center;
  padding-top: 14px;
  font-family: 'Source Code Pro', monospace;
  font-size: 14px;
}

.message__icon {
  font-size: 18px;
}

@media screen and (max-width: 560px) {
  .apis_counter {
    padding: 8px 12px 0px;
  }
  .apis_category {
    padding: 8px 0px 0px;
  }
  .apis_category__name {
    padding: 8px 12px 0px;
  }
}
